<template>
    <pl-dom v-if="p_init">
        <transition :name="`pl-popover-animate-${animate}`">
            <div class="pl-popper" v-show="p_value" :class="classes" :style="styles" ref="popper">
                <slot></slot>
            </div>
        </transition>
    </pl-dom>
</template>

<script>

    import Popper from 'popper.js'
    import {MountedMixin, ValueMixin} from "../../mixin/component-mixin";
    import PlDom from "../pl-dom";

    const POPOVER_DIRECTION = {
        TOP: 'top',
        BOTTOM: 'bottom',
        LEFT: 'left',
        RIGHT: 'right',
    }
    const POPOVER_ALIGN = {
        START: 'start',
        CENTER: 'center',
        END: 'end',
    }

    export default {
        name: "pl-popper",
        components: {PlDom},
        mixins: [MountedMixin, ValueMixin],
        props: {
            reference: {},

            direction: {type: String, default: POPOVER_DIRECTION.BOTTOM},           //top|bottom|left|right
            align: {type: String, default: POPOVER_ALIGN.START},                    //
            arrow: {type: Boolean},                                                 //
            offset: {type: Number, default: 2},                                       //
            animate: {type: String, default: 'drop'},                               //
            height: {default: 180},                                                 //
            width: {default: 180},                                                  //
            windowBoundary: {type: Boolean, default: true},                         //window
            disabledEqual: {type: Boolean},                                         //
            disabledHideOnClickOutside: {type: Boolean},                            //
            relate: {type: Array, default: () => []},                               //relate
            zIndex: {type: Number, default: 9999},                                  //zIndex

            onOpen: {type: Function},                                               //
            onClose: {type: Function},                                              //
            onShow: {type: Function},                                               //
            onHide: {type: Function},                                               //
        },
        watch: {
            value(val) {
                if (!!val) this.show()
                else this.hide()
            },
            direction(val) {
                this.p_direction = val
                this.init()
            },
            align(val) {
                this.p_align = val
                this.init()
            },
            arrow() {
                this.init()
            },
            reference() {
                this.init()
            },
        },
        data() {
            return {
                p_open: false,
                p_show: false,

                p_init: false,
                p_popper: null,
                p_direction: this.direction,
                p_align: this.align,
                p_zIndex: null,
            }
        },
        computed: {
            classes() {
                return [
                    {
                        'pl-popper-arrow': !!this.arrow,
                    },
                    `pl-popper-${this.p_direction}-${this.p_align}`,
                ]
            },
            styles() {
                if (!this.p_mounted || !this.referenceEl) return
                const ret = {}
                !!this.height && (ret.height = this.$plain.$utils.unit(this.height))
                !!this.width && (ret.width = this.$plain.$utils.unit(this.width))
                if (!this.disabledEqual && this.p_mounted) ret[this.p_vertical ? 'width' : 'height'] = `${this.referenceEl[this.p_vertical ? 'offsetWidth' : 'offsetHeight']}px`
                ret.zIndex = this.p_zIndex + this.zIndex
                return ret
            },
            p_vertical() {
                return this.$plain.$utils.oneOf(this.p_direction, ['top', 'bottom'])
            },
            p_relate() {
                return [this.referenceEl, this.$refs.popper, ...(this.relate || [])]
            },
            referenceEl() {
                if (!this.p_mounted) return
                return !this.reference ? null : (this.reference.$el || this.reference)
            },
        },
        methods: {
            async show() {
                if (!this.p_init) {
                    this.p_init = true
                    await this.$plain.nextTick()
                }
                if (!this.p_popper) this.init()
                this.p_value = true
                await this.$plain.nextTick()
                this.p_popper.update()
                this.p_zIndex = this.$plain.getZIndex()
                this.$emit('show')
                this.p_show = true
                !!this.onShow && this.onShow()
                this.pl_event()
            },
            async hide() {
                this.p_value = false
                await this.$plain.nextTick()
                this.$emit('hide')
                !!this.onHide && this.onHide()
                this.pl_event()
            },
            async toggle() {
                return this.p_value ? (await this.hide()) : (await this.show())
            },
            reload() {
                this.init()
            },
            init() {
                if (!this.p_init) return
                this.destroy()
                this.p_popper = new Popper(this.referenceEl, this.$refs.popper, {
                    placement: `${this.p_direction}-${this.p_align}`,
                    modifiers: {
                        offset: {offset: `0,${this.offset == null ? this.arrow ? 10 : '0' : this.offset}`,},
                        preventOverflow: this.windowBoundary ? {boundariesElement: 'window'} : null,
                        computeStyle: {gpuAcceleration: false},
                    },
                    onUpdate: () => this.p_refresh(),
                    onCreate: () => this.p_refresh(),
                })
                window.addEventListener('click', this.p_clickWindow)
            },
            destroy() {
                if (!!this.p_popper) {
                    this.p_popper.destroy()
                    this.p_popper = null
                }
                window.removeEventListener('click', this.p_clickWindow)
            },
            p_refresh() {
                let placement = this.p_popper.popper.getAttribute('x-placement').split('-');
                this.p_direction = placement[0];
                this.p_align = placement[1];
            },
            pl_event() {
                setTimeout(() => {
                    this.p_open = this.p_value
                    if (this.p_open) {
                        !!this.onOpen && (this.onOpen())
                        this.$emit('open')
                        this.$emit('input', true)
                    } else {
                        !!this.onClose && (this.onClose())
                        this.$emit('close')
                        this.$emit('input', false)
                        this.p_show = false
                    }
                }, 250)
            },
            async p_clickWindow(e) {
                if (!this.disabledHideOnClickOutside && !this.p_relate.some(el => el.contains(e.target))) this.hide()
            },
        },
        beforeDestroy() {
            this.destroy()
        },
    }
</script>

<style lang="scss">

    $popper-arrow-size: 6px;
    $popper-back-ground: white;
    $popper-scale-animates: (
            top-start:(
                    transform-origin:bottom left,
                    active-transform:scaleY(1),
                    inactive-transform:scaleY(0),
                    direction:'bottom',
                    align:'left',
                    alignValue:16px,
                    zeroWidth:'border-bottom-width',
                    borderColor:'border-top-color',
            ),
            top-center:(
                    transform-origin:bottom center,
                    active-transform:scaleY(1),
                    inactive-transform:scaleY(0),
                    direction:'bottom',
                    align:'left',
                    alignValue:calc(50% - #{$popper-arrow-size/2}),
                    zeroWidth:'border-bottom-width',
                    borderColor:'border-top-color',
            ),
            top-end:(
                    transform-origin:bottom right,
                    active-transform:scaleY(1),
                    inactive-transform:scaleY(0),
                    direction:'bottom',
                    align:'right',
                    alignValue:16px,
                    zeroWidth:'border-bottom-width',
                    borderColor:'border-top-color',
            ),
            bottom-start:(
                    transform-origin:top left,
                    active-transform:scaleY(1),
                    inactive-transform:scaleY(0),
                    direction:'top',
                    align:'left',
                    alignValue:16px,
                    zeroWidth:'border-top-width',
                    borderColor:'border-bottom-color',
            ),
            bottom-center:(
                    transform-origin:top center,
                    active-transform:scaleY(1),
                    inactive-transform:scaleY(0),
                    direction:'top',
                    align:'left',
                    alignValue:calc(50% - #{$popper-arrow-size/2}),
                    zeroWidth:'border-top-width',
                    borderColor:'border-bottom-color',
            ),
            bottom-end:(
                    transform-origin:top right,
                    active-transform:scaleY(1),
                    inactive-transform:scaleY(0),
                    direction:'top',
                    align:'right',
                    alignValue:16px,
                    zeroWidth:'border-top-width',
                    borderColor:'border-bottom-color',
            ),
            left-start:(
                    transform-origin:right top,
                    active-transform:scaleX(1),
                    inactive-transform:scaleX(0),
                    direction:'right',
                    align:'top',
                    alignValue:16px,
                    zeroWidth:'border-right-width',
                    borderColor:'border-left-color',
            ),
            left-center:(
                    transform-origin:right center,
                    active-transform:scaleX(1),
                    inactive-transform:scaleX(0),
                    direction:'right',
                    align:'top',
                    alignValue:calc(50% - #{$popper-arrow-size/2}),
                    zeroWidth:'border-right-width',
                    borderColor:'border-left-color',
            ),
            left-end:(
                    transform-origin:right bottom,
                    active-transform:scaleX(1),
                    inactive-transform:scaleX(0),
                    direction:'right',
                    align:'bottom',
                    alignValue:16px,
                    zeroWidth:'border-right-width',
                    borderColor:'border-left-color',
            ),
            right-start:(
                    transform-origin:left top,
                    active-transform:scaleX(1),
                    inactive-transform:scaleX(0),
                    direction:'left',
                    align:'top',
                    alignValue:16px,
                    zeroWidth:'border-left-width',
                    borderColor:'border-right-color',
            ),
            right-center:(
                    transform-origin:left center,
                    active-transform:scaleX(1),
                    inactive-transform:scaleX(0),
                    direction:'left',
                    align:'top',
                    alignValue:calc(50% - #{$popper-arrow-size/2}),
                    zeroWidth:'border-left-width',
                    borderColor:'border-right-color',
            ),
            right-end:(
                    transform-origin:left bottom,
                    active-transform:scaleX(1),
                    inactive-transform:scaleX(0),
                    direction:'left',
                    align:'bottom',
                    alignValue:16px,
                    zeroWidth:'border-left-width',
                    borderColor:'border-right-color',
            ),
    );

    .pl-popper {
        position: relative;
        box-sizing: border-box;
        transition: .25s cubic-bezier(.24, .22, .015, 1.56);
        transition-property: transform, opacity;
        border-radius: 4px;
        z-index: 99999;
        word-break: break-word;
        background-color: $popper-back-ground;

        @each $key, $type in $popper-scale-animates {
            $type-object: map_get($popper-scale-animates, $key);
            &.pl-popper-#{$key} {
                box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
                border: 1px solid #e4e7ed;
                border-radius: 4px;
                transform-origin: map_get($type-object, transform-origin);

                &.pl-popover-animate-drop-enter-active, &.pl-popover-animate-drop-leave-active {
                    transform: map_get($type-object, active-transform);
                    opacity: 1;
                }

                &.pl-popover-animate-drop-enter, &.pl-popover-animate-drop-leave-to {
                    transform: map_get($type-object, inactive-transform);
                    opacity: 0;
                }

                &.pl-popover-animate-scale-enter-active, &.pl-popover-animate-scale-leave-active {
                    transform: scale(1);
                    opacity: 1;
                }

                &.pl-popover-animate-scale-enter, &.pl-popover-animate-scale-leave-to {
                    transform: scale(0.6);
                    opacity: 0;
                }

                &.pl-popper-arrow {
                    &::after {
                        border: $popper-arrow-size solid transparent;
                        content: '';
                        position: absolute;
                        display: block;
                        width: 0;
                        height: 0;

                        #{map_get($type-object, direction)}: -5px;
                        #{map_get($type-object, align)}: #{map_get($type-object, alignValue)};
                        #{map_get($type-object, zeroWidth)}: 0;
                        #{map_get($type-object, borderColor)}: white;
                    }
                }
            }
        }
    }
</style>