import $utils from '../../../src/scripts/utils'

/*mixin*/
const TableMixin = {
    props: {
        /*base table*/
        data: {type: Array, default: () => []},         //
        showNum: {type: Number, default: 10},           //
        bodyRowHeight: {type: Number, default: 36},     //
        headRowHeight: {type: Number, default: 40},     //
        noHeader: {type: Boolean},                      //
        fitParent: {type: Boolean},                     //
        sortField: {type: String},                      //
        sortDesc: {type: Boolean, default: true},       //

        /*base table*/
        headColumns: {type: Array},                     //
        bodyColumns: {type: Array},                     //
        fixedExist: {type: Object},                     //
        fixed: {type: String, default: 'center'},       //:left,center,right
        hostWidth: {type: Number},                      //
        hoverIndex: {type: Number},                          //
    },
}

const ColumnPublicProps = {
    title: {type: String},                              //
    field: {type: String},                              //
    width: {default: '200px'},                          //
    fit: {type: Number, default: 0},                    //
    order: {type: Number, default: 0},                  //
    fixed: {type: String, default: 'center'},           //
    placeLeft: {type: Boolean},                         //
    placeRight: {type: Boolean},                        //
    hide: {type: Boolean},                              //
    // disabledConfig: {type: Boolean},                    //
    align: {type: String, default: 'left'},             //

    search: {type: Boolean, default: true},             //
    searchField: {type: Boolean},                       //
    sort: {type: Boolean, default: true},               //
    sortField: {type: Boolean},                         //
    editable: {type: Boolean, default: true},           //
    editableFunc: {type: Function},                     //
    prop: {type: Object},                               //

    dataType: {type: String},                           //:tel,cny,money,percent
    formatter: {type: Function},                        //
    tooltip: {type: Boolean},                           //tooltip
    link: {type: Boolean},                              //
    click: {type: Function},                            //
    showInDialog: {type: Boolean},                      //dialog

    required: {type: Boolean},                          //
    rules: {type: Array},                               //
    validFunc: {type: Function},                        //
    validOnInit: {type: Boolean, default: true},        //
    requiredFunc: {type: Function},                     //

    /*----------------------------------------------------------------------------------*/

    quickFilter: {type: Boolean, default: false},       //
    filterName: {type: String, default: 'input'},       //
    filterOption: {type: Object},                       //
    lov: {type: String},                                //

    /*----------------------------------------------------------------------------------*/
    scopedSlots: {type: Object},                        //
    renderNormal: {type: Function},                     //:
    renderEdit: {type: Function},                       //
    renderHead: {type: Function},                       //
}

const RefreshProps = [
    'fit',
    'order',
    'fixed,',
    'placeLeft',
    'placeRight',
    'hide',
    'disabledConfig',
    'scopedSlots',
    'renderNormal',
    'renderEdit',
    'renderHead',
]

const PublicColumnMixin = {
    props: ColumnPublicProps,
    computed: {
        columnBinding() {
            return Object.keys(ColumnPublicProps).reduce((ret, key) => {
                ret[key] = this[key]
                return ret
            }, {})
        },
    },
}

const ColumnGroupMixin = {
    props: {
        title: {},                                  //
        order: {type: Number, default: 0},          //
        fixed: {type: String, default: 'center'},   //
        placeLeft: {type: Boolean},                 //
        placeRight: {type: Boolean},                //
        /**/
        isRoot: {type: Boolean},                    //
    },
}

class TableColumn {
    constructor(context) {
        const originalProps = Object.keys(ColumnPublicProps).reduce((ret, key) => {
            // context.$set(ret, key, context[key])
            ret[key] = context[key]
            return ret
        }, {})
        originalProps.width = context.$plain.$utils.removePx(originalProps.width)
        originalProps.id = context.$plain.$utils.uuid()
        this.originalProps = originalProps
        Object.assign(this, originalProps)
    }
}

class TableColumnGroup {
    constructor(context) {
        const originalProps = Object.keys(ColumnGroupMixin.props).reduce((ret, key) => {
            // context.$set(ret, key, context[key])
            ret[key] = context[key]
            return ret
        }, {})
        this.originalProps = originalProps
        Object.assign(this, originalProps)
        this.group = true
    }
}

class RowData {
    constructor(row, index, idField) {
        const id = row[idField]
        if (!id) {
            throw new Error(`The key field ${idField} is empty in ${JSON.stringify(row)}`)
        }
        Object.assign(this, {
            row,
            index,
            editRow: $utils.deepCopy(row),
            id,
            check: false,
            editable: false,
            hover: false,
        })
    }
}

export {
    RowData,
    TableMixin,
    PublicColumnMixin,
    ColumnGroupMixin,
    TableColumn,
    TableColumnGroup,
    RefreshProps,
}