<template>
    <pl-dom :value="transferDom">
        <transition :name="transition">
            <div class="pl-dialog"
                 :class="classes"
                 v-show="p_value"
                 :style="styles"
                 @click="p_clickShadow"
                 @mouseenter="pl_mouseenter"
                 @mouseleave="pl_mouseleave">
                <div class="pl-dialog-content"
                     ref="content"
                     v-if="p_initialized"
                     @click.stop="p_clickContent"
                     :style="contentStyles">
                    <div class="pl-dialog-head" v-if="!noHeader">
                        <div class="pl-dialog-title">
                            <pl-icon :icon="TYPE[type]" v-if="!!type" class="pl-dialog-type-icon"/>
                            <span>{{title}}</span>
                        </div>
                        <div class="pl-dialog-head-operator">
                            <!--<pl-icon icon="pl-minimize"/>-->
                            <pl-icon icon="pad-plus-circle-fill" v-show="!p_isFull && !!max" @click="p_isFull=true"/>
                            <pl-icon icon="pad-minus-circle-fill" v-show="!!p_isFull && !!max" @click="p_isFull=false"/>
                            <pl-icon icon="pad-close-circle-fill" @click="hide" class="pl-dialog-close-icon" v-if="!noClose" hover/>
                        </div>
                    </div>
                    <div class="pl-dialog-body" :style="bodyStyles">
                        <slot></slot>
                    </div>
                    <div class="pl-dialog-foot" :class="[`pl-dialog-foot-align-${footAlign}`]" v-if="!noFooter && (cancelButton || confirmButton || !!$slots.foot)">
                        <slot name="foot">
                            <pl-button box-type="line" label="" @click="p_cancel" v-if="!!cancelButton"/>
                            <pl-button label="" @click="p_confirm" v-if="!!confirmButton"/>
                        </slot>
                    </div>
                </div>
            </div>
        </transition>
    </pl-dom>
</template>

<script>
    import {ValueMixin} from "../../mixin/component-mixin";
    import keyboard from 'src/scripts/keyboard'
    import PlIcon from "../pl-icon";
    import PlButton from "../pl-button";
    import PlDom from "../pl-dom";

    export default {
        name: "pl-dialog",
        components: {PlDom, PlButton, PlIcon},
        mixins: [ValueMixin],
        props: {
            type: {type: String, default: 'info'},                                              // info|success|warn|error|help
            title: {type: String, default: ''},                                             //
            shape: {type: String, default: 'fillet'},                                             // none|fillet
            shadowColor: {type: String, default: 'rgba(0,0,0,0.25)'},                           //
            disabledHideOnClickShadow: {type: Boolean,},                                        //
            transition: {type: String, default: 'pl-dialog-animate-drop'},                      //
            height: {type: String | Number,},                                                   //
            minHeight: {type: String | Number},                                                 //
            maxHeight: {type: String | Number},                                                 //
            width: {type: String | Number,},                                                    //
            minWidth: {type: String | Number},                                                  //
            maxWidth: {type: String | Number},                                                  //
            full: {type: Boolean},                                                              //
            confirmButton: {type: Boolean},                                                     //
            cancelButton: {type: Boolean},                                                      //
            noClose: {type: Boolean},                                                           //
            dialogClass: {type: String,},                                                       //bodyclass
            vertical: {type: String},                                                           // start|center|end
            horizontal: {type: String},                                                         // start|center|end
            initialized: {type: Boolean,},                                                      //
            destroyOnHide: {type: Boolean},                                                     //
            transferDom: {type: Boolean},                                                       //body
            max: {type: Boolean},                                                               //
            noHeader: {type: Boolean},                                                          //
            noFooter: {type: Boolean},                                                          //
            noPadding: {type: Boolean},                                                         //
            // min: {type: Boolean},
            // remove: {type: Boolean},
            top: {type: Number | String,},                                                      //
            bottom: {type: Number | String},                                                    //
            left: {type: Number | String},                                                      //
            right: {type: Number | String},                                                     //
            footAlign: {type: String, default: 'center'},                                       //left|center|right

            enterToConfirm: {type: Boolean, default: true},                                     //confirm
            spaceToConfirm: {type: Boolean, default: true},                                     //confirm
            escToCancel: {type: Boolean, default: true},                                        //Esccancel

            disabledHideOnConfirm: {type: Boolean},                                             //
            disabledHideOnCancel: {type: Boolean},                                              //
        },
        data() {
            return {
                TYPE: Object.keys(this.$plain.TYPE).reduce((ret, key) => {
                    ret[key] = this.$plain.TYPE[key].icon
                    return ret
                }, {}),
                p_index: 0,
                p_watchValue: false,
                p_isFull: this.full,
                p_initialized: this.initialized || this.value,
                p_activeElement: null,

                keyboardListener: {
                    'enter': () => {
                        !!this.enterToConfirm && this.p_confirm()
                    },
                    'space': () => {
                        !!this.spaceToConfirm && this.p_confirm()
                    },
                    'esc': () => {
                        !!this.escToCancel && this.p_cancel()
                    },
                }
            }
        },
        watch: {
            value(val) {
                if (val !== this.p_value) this.show(val)
            },
        },
        computed: {
            classes() {
                return [
                    {
                        'pl-dialog-full-size': this.full || this.p_isFull,
                        'pl-dialog-padding': !this.noPadding,
                    },
                    `pl-dialog-type-${this.type}`,
                    `pl-dialog-shape-${this.shape}`,
                    `pl-dialog-vertical-${this.vertical || 'start'}`,
                    `pl-dialog-horizontal-${this.horizontal || 'center'}`,
                    this.dialogClass,
                ]
            },
            styles() {
                return {
                    backgroundColor: this.shadowColor,
                    zIndex: this.p_index,
                }
            },
            bodyStyles() {
                const styles = {};
                ['width', 'height', 'minWidth', 'minHeight', 'maxWidth', 'maxHeight'].forEach(prop => !!this[prop] && (styles[prop] = this.$plain.$utils.unit(this[prop])))
                if (!this.width && !this.minWidth && !this.maxWidth) {
                    styles.minWidth = '336px'
                    styles.maxWidth = '500px'
                }
                if (!this.height && !this.minHeight) styles.minHeight = '88px'
                return styles
            },
            contentStyles() {
                const styles = {}
                styles.top = `${this.top != null ? this.top : !this.vertical && !this.horizontal ? '10vh' : 0}`
                this.left != null && (styles.left = this.$plain.$utils.unit(this.left))
                this.bottom != null && (styles.bottom = this.$plain.$utils.unit(this.bottom))
                this.right != null && (styles.right = this.$plain.$utils.unit(this.right))
                return styles
            },
        },
        methods: {
            show(val = true) {
                const next = () => {
                    this.p_index = this.$plain.getZIndex()
                    this.p_value = val
                }
                if (!this.p_initialized) {
                    this.p_initialized = true
                    this.$nextTick(() => next())
                } else next()

                this.p_activeElement = document.activeElement
                !!this.p_activeElement && this.p_activeElement.blur()
            },
            hide() {
                this.p_value = false
                !!this.destroyOnHide && setTimeout(() => this.p_initialized = false, this.$plain.transitionTime)
                !!this.p_activeElement && this.p_activeElement.focus()
            },
            p_clickShadow(e) {
                if (!this.$refs.content.contains(e.target)) {
                    this.$emit('clickShadow', e)
                    if (!this.disabledHideOnClickShadow) {
                        this.p_cancel()
                    }
                }
            },
            p_clickContent(e) {
                this.$emit('clickContent', e)
            },
            p_confirm() {
                this.$emit('confirm')
                !this.disabledHideOnConfirm && this.hide()
            },
            p_cancel() {
                this.$emit('cancel')
                !this.disabledHideOnCancel && this.hide()
            },
            pl_mouseenter() {
                keyboard.addListener(this.keyboardListener)
            },
            pl_mouseleave() {
                keyboard.removeListener(this.keyboardListener)
            },
        },
        beforeDestroy() {
            keyboard.removeListener(this.keyboardListener)
        },
    }
</script>