<template>
    <div class="pl-dropdown">
        <div ref="reference"
             class="pl-dropdown-reference"
             @click="p_click"
        >
            <slot></slot>
        </div>
        <pl-popover ref="popover"
                    :reference="!p_mounted?null:$refs.reference"
                    v-bind="popoverBinding"
                    v-if="p_init">
            <div class="pl-dropdown-popper" ref="popper">
                <slot name="popper"></slot>
            </div>
        </pl-popover>
    </div>
</template>

<script>
    import {MountedMixin} from "../../mixin/component-mixin";
    import PlPopover from "../popper/pl-popover";

    export default {
        name: "pl-dropdown",
        components: {PlPopover},
        mixins: [MountedMixin],
        props: {
            popover: {},
            init: {type: Boolean},
        },
        data() {
            return {
                p_popover: null,
                p_init: this.init,
            }
        },
        computed: {
            popoverBinding() {
                return Object.assign({
                    popper: {
                        arrow: true,
                        disabledEqual: true,
                        height: '200px',
                        width: '150px',
                    },
                }, this.popover)
            },
        },
        methods: {
            async show() {
                this.p_popover.show()
            },
            hide() {
                this.p_popover.hide()
            },
            async p_click() {
                if (!this.p_init) {
                    this.p_init = true
                    await this.$plain.nextTick()
                    this.p_popover = this.$refs.popover
                }

                if (!!this.p_popover.p_popper.p_value) {
                    this.p_popover.hide()
                } else {
                    this.p_popover.show()
                }
            },
        },
    }
</script>

<style lang="scss">
    @include themeWrap {
        .pl-dropdown {
            display: inline-block;
            vertical-align: middle;
            outline: none;

            .pl-dropdown-popper-wrapper {
                display: none;
            }
        }
        .pl-dropdown-popper {
            display: inline-flex;
            flex-direction: column;
            box-sizing: border-box;
            height: 100%;
            width: 100%;
            padding: 3px 3px;

            .pl-dropdown-item {
                .pl-dropdown-item-content {
                    padding: 6px 6px;
                    font-size: 12px;
                    color: plVar(colorContent);
                    border-radius: plVar(borderFillet);

                    &:hover {
                        cursor: pointer;
                        background-color: plVar(colorPrimaryLighter);
                        color: plVar(colorPrimaryDeep);
                    }

                    .pl-dropdown-item-icon {
                        margin-right: 6px;
                    }
                }

                .pl-dropdown-item-line {
                    width: 100%;
                    height: 1px;
                    background-color: plVar(colorBorder);
                    margin: 6px 0;
                }
            }
        }
    }
</style>